.footer{margin-top:auto;background:var(--color-text);color:hsla(0,0%,100%,.8);padding:1rem 1rem}.inner{margin:0 auto;display:flex;flex-wrap:wrap;justify-content:space-between;align-items:center;gap:1rem 1.5rem}.brandBlock{display:flex;flex-direction:column;gap:.35rem}.brand{font-size:1.1rem;font-weight:500;letter-spacing:.18em;text-transform:uppercase}.tagline{font-size:.85rem;color:hsla(0,0%,100%,.6)}.meta{display:flex;flex-direction:column;gap:.35rem;align-items:flex-end}.contact{display:flex;gap:1.25rem;align-items:center}.contactLink{font-size:.9rem;color:hsla(0,0%,100%,.8);text-decoration:none;transition:color .2s ease}.contactLink:hover{color:#9ecde0}.copy{font-size:.75rem;color:hsla(0,0%,100%,.45)}@media(max-width: 640px){.footer{padding:1.5rem 1.25rem}.inner{flex-direction:column;align-items:flex-start}.meta{align-items:flex-start;gap:.5rem}.contact{flex-direction:column;align-items:flex-start;gap:.35rem}}